.nav {
  position: fixed;
  top: 0;
  padding: 15px;
  width: 100%;
  z-index: 1;
  height: 50px;
  left: 0;

  /*Animation of Nav*/
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav__contents {
  display: flex;
  justify-content: space-between;
}

.nav__logo {
  width: 80px;
  position: fixed;
  left: 0;
  top: 5px;
  padding-left: 20px;
  object-fit: contain;
  cursor: pointer;
}

.nav__avatar {
  position: fixed;
  right: 20px;
  width: 50px;
  cursor: pointer;
}

.nav__black {
  background-color: #111;
}
